@mixin vsphereNeutralRowTable () {
   @include vsphereTable();
}

@mixin vsphereHoveredRowTable () {
   @include vsphereTable($vsphere-hover-row-text-color, $vsphere-hover-row-bg-color);
}

@mixin vsphereSelectedRowTable () {
   @include vsphereTable($vsphere-selected-row-text-color, $vsphere-selected-row-bg-color);
}

@mixin vsphereTable (
   $text-color: $vsphere-neutral-row-text-color,
   $background-color: $vsphere-neutral-row-bg-color
) {
   .table {
      &,
      th {
         color: $text-color;
         background-color: $background-color;
      }
   }
}
