//Headings, Paragraphs
@mixin vsphereRowTypography($typoColor) {
   h1, h2, h3, h4, h5, h6 {
      color: $typoColor;
   }
   p {
      color: $typoColor;
   }

   .p0,
   p.p0 {
      color: $typoColor;
   }

   .p2,
   p.p2 {
      color: $typoColor;
   }

   .p3,
   p.p3 {
      color: $typoColor;
   }

   .p4,
   p.p4 {
      color: $typoColor;
   }

   .p5,
   p.p5 {
      color: $typoColor;
   }

   .p6,
   p.p6 {
      color: $typoColor;
   }

   .p7,
   p.p7 {
      color: $typoColor;
   }

   .p8,
   p.p8 {
      color: $typoColor;
   }
}
@mixin vsphereNeutralRowTypography() {
   @include vsphereRowTypography($vsphere-neutral-row-text-color);
}

@mixin vsphereHoveredRowTypography() {
   @include vsphereRowTypography($vsphere-hover-row-text-color);
}

@mixin vsphereSelectedRowTypography() {
   @include vsphereRowTypography($vsphere-selected-row-text-color);
}
