@mixin sideNavLinkAndContent () {
  color: #{$vsphere-neutral-row-text-color};
  border-bottom: $vsphere-table-borderwidth solid transparent;
  .btn-link, a {
    color: #{$vsphere-neutral-row-link-color};
  }
  @include vsphereNeutralRowClrIcon();

  &:hover:not(.active) {
    background: $vsphere-hover-row-bg-color;
    color: #{$vsphere-hover-row-text-color};
    border-bottom: $vsphere-table-borderwidth solid $vsphere-hover-row-border-bottom-color;
    .btn-link, a {
      color: #{$vsphere-hover-row-link-color};
    }
    @include vsphereHoveredRowClrIcon();
  }

  &.active {
    color: $vsphere-selected-row-text-color;
    background: $vsphere-selected-row-bg-color;
    border-bottom: $vsphere-table-borderwidth solid transparent;
    .btn-link, a {
      color: #{$vsphere-selected-row-link-color};
    }
    @include vsphereSelectedRowClrIcon();
  }
}

.sidenav {
  .sidenav-content {
    & > .nav-link {
      @include sideNavLinkAndContent();
    }
  }

  .nav-group {
    .nav-list {
      .nav-link {
        @include sideNavLinkAndContent();
      }
    }
  }
}
