@mixin textarea (
   $text-color,
   $bg-color,
   $border-bottom-width,
   $border-color,
   $validation-error-color
) {
   textarea, .clr-textarea {
      color: $text-color;
      background: $bg-color;
      border: $border-bottom-width solid $border-color;

      // Dark theme special handling
      //@if (not $vsphere-css-custom-properties) {
         &:focus {
            box-shadow: 0 0 0;
         //}
      }
      @include placeholderFocused($text-color);
   }

   .clr-error .clr-textarea {
      border-color: $validation-error-color;

      &:focus {
         outline: 0;
         box-shadow: 0 0 0;
      }
   }
}
