@mixin vsphereNeutralRowButton () {
   @include vsphereButton();
}

@mixin vsphereHoveredRowButton () {
   @include vsphereButton($vsphere-hover-row-text-color);
}

@mixin vsphereSelectedRowButton () {
   @include vsphereButton($vsphere-selected-row-text-color);
}

@mixin vsphereButton ($text_color: $vsphere-neutral-row-text-color) {
   .dropdown-toggle:not(.btn):not(.btn-link) {
      clr-icon[shape*="caret"] {
         color: $text_color;
      }
   }
}