header,
.header {
   clr-icon {
      fill: $vsphere-header-neutral-text-color;
   }
   .search-box, /* TODO: deprecated. Remove when the clarity remove it.*/
   .search {
      .box-container {
         background-color: inherit;
         opacity: 0.65;
         clr-icon {
            fill: $vsphere-header-neutral-text-color;
            stroke: $vsphere-header-neutral-text-color;
         }
         input[type='text'] {
            color: $vsphere-header-neutral-text-color;
         }
      }
      .box-container-highlight {
         background-color: $vsphere-neutral-row-bg-color;
         color: $vsphere-neutral-row-text-color;
         opacity: 1;
         clr-icon {
            fill: $vsphere-neutral-row-text-color;
            stroke: $vsphere-neutral-row-text-color;
         }
         input[type='text'] {
            &:focus:not([readonly])::placeholder {
               color: $vsphere-neutral-row-text-color;
            }
            color: $vsphere-neutral-row-text-color;
         }
      }
   }
}