@mixin toggle ($border-color) {
   .clr-toggle-wrapper {
      input[type=checkbox] + label::before {
         border-color: $border-color;
      }
      input[type=checkbox]:focus + label::before {
         box-shadow: 0 0 0;
      }
   }
}
