//Clarity patches: for not supported states
@mixin nestableDatagrid(
   $parentRowState: $vsphere-row-neutral,
   $nest-level: $vsphere-max-nest-level
) {
   $text-color: colorForState('text', $parentRowState);
   $link-color: colorForState('link', $parentRowState);
   $bg-color: colorForState('bg', $parentRowState);
   $border-color: colorForState('border', $parentRowState);
   .datagrid {
      .datagrid-row {
         background-color: $bg-color;
         color: $text-color;
         border-bottom: $vsphere-table-borderwidth solid $border-color;
         border-top: none;
         .datagrid-column {
            color: $text-color;
            background-color: $bg-color;
            border-bottom-color: $border-color;
            .datagrid-column-title {
               .sort-icon {
                  fill: $text-color;
               }
               color: $text-color;
               background-color: $bg-color;
               border-bottom-color: $border-color;
            }
            .datagrid-filter-toggle {
               clr-icon[shape='filter-grid'] {
                  fill: $text-color;
               }
            }
         }

         &:hover:not(.datagrid-selected) {
            background-color: $vsphere-hover-row-bg-color;
            border-bottom: $vsphere-table-borderwidth solid $vsphere-hover-row-border-bottom-color;

            background-color: $vsphere-hover-row-bg-color;
            color: $vsphere-hover-row-text-color;
            @if ($nest-level > 0) {
               @include hoveredRowChildElements();
               @include nestableContainerElements($vsphere-row-hover, $nest-level - 1);
            }

            .datagrid-expandable-caret {
               .datagrid-expandable-caret-icon {
                  color: $vsphere-hover-row-text-color;
               }
            }
         }

         &.datagrid-selected {
            background-color: $vsphere-selected-row-bg-color;
            border-bottom: $vsphere-table-borderwidth solid $vsphere-table-border-color;
            color: $vsphere-selected-row-text-color;

            @if ($nest-level > 0) {
               @include selectedRowChildElements();
               @include nestableContainerElements($vsphere-row-selected, $nest-level - 1);
            }

            .datagrid-expandable-caret {
               .datagrid-expandable-caret-icon {
                  color: $vsphere-selected-row-text-color;
               }
            }
         }
      }

      .datagrid-table {
         // Placeholder
         .datagrid-placeholder-container {
            border-top-color: $border-color;
         }
         .datagrid-placeholder {
            background: $bg-color;
            &.datagrid-empty {
               color: $text-color;
            }
         }
      }

      .datagrid-header {
         .datagrid-row {
            &:hover {
               border-bottom: $vsphere-table-borderwidth solid $vsphere-table-border-color;
            }
            border-bottom: $vsphere-table-borderwidth solid $vsphere-table-border-color;
         }
      }
   }

   .datagrid-filter {
      background: $bg-color;
      border-color: $border-color;
   }
}
