@mixin vsphereBadge(
   $info-color: $vsphere-neutral-row-emphasis-info,
   $success-color: $vsphere-neutral-row-emphasis-success,
   $warning-color: $vsphere-neutral-row-emphasis-warning,
   $danger-color: $vsphere-neutral-row-emphasis-danger,
   $purple-color: $vsphere-neutral-row-emphasis-purple,
   $blue-color: $vsphere-neutral-row-emphasis-blue,
   $orange-color: $vsphere-neutral-row-emphasis-orange,
   $light-blue-color: $vsphere-neutral-row-emphasis-light-blue,
   $gray-color: $vsphere-neutral-row-emphasis-gray,
   $gray-color-inverted: $vsphere-neutral-row-emphasis-gray-inverted) {
   .badge {
      &,
      &.badge-gray,
      &.badge-1 {
         color: $gray-color-inverted;
         background: $gray-color;
      }

      &.badge-purple,
      &.badge-2 {
         color: $gray-color-inverted;
         background: $purple-color;
      }
      &.badge-blue,
      &.badge-3 {
         color: $gray-color-inverted;
         background: $blue-color;
      }
      &.badge-orange,
      &.badge-4 {
         color: $gray-color-inverted;
         background: $orange-color;
      }
      &.badge-light-blue,
      &.badge-5 {
         color: $inverted-gray;
         background: $light-blue-color;
      }
      &.badge-info {
         color: $gray-color-inverted;
         background: $info-color;
      }
      &.badge-success {
         color: $gray-color-inverted;
         background: $success-color;
      }
      &.badge-warning {
         color: $gray-color-inverted;
         background: $warning-color;
      }
      &.badge-danger {
         color: $gray-color-inverted;
         background: $danger-color;
      }
   }
}

@mixin vsphereNeutralRowBadge() {
   @include vsphereBadge();
}

@mixin vsphereHoveredRowBadge() {
   @include vsphereBadge(
         $vsphere-hover-row-emphasis-info,
         $vsphere-hover-row-emphasis-success,
         $vsphere-hover-row-emphasis-warning,
         $vsphere-hover-row-emphasis-danger,
         $vsphere-hover-row-emphasis-purple,
         $vsphere-hover-row-emphasis-blue,
         $vsphere-hover-row-emphasis-orange,
         $vsphere-hover-row-emphasis-light-blue,
         $vsphere-hover-row-emphasis-gray,
         $vsphere-hover-row-emphasis-gray-inverted);
}

@mixin vsphereSelectedRowBadge() {
   @include vsphereBadge(
         $vsphere-selected-row-emphasis-info,
         $vsphere-selected-row-emphasis-success,
         $vsphere-selected-row-emphasis-warning,
         $vsphere-selected-row-emphasis-danger,
         $vsphere-selected-row-emphasis-purple,
         $vsphere-selected-row-emphasis-blue,
         $vsphere-selected-row-emphasis-orange,
         $vsphere-selected-row-emphasis-light-blue,
         $vsphere-selected-row-emphasis-gray,
         $vsphere-selected-row-emphasis-gray-inverted);
}