@import "clr-ui-a11y.constants";

$vsphere-css-custom-properties: false;

// Common colors
$light-gray: #324F61;
$inverted-gray: #D8E3E9;
$emphasis-color: #FFFFFF;

// Blue
$light-blue: #00648F; // TODO maybe wrong in UX figma

// Globals
$vsphere-global-bg-color: #1B2A32;

// Neutral row colors
$vsphere-neutral-row-bg-color: #29414E;
$vsphere-neutral-row-text-color: #E9ECEF;
$vsphere-neutral-row-link-color: #79C6E6;
$vsphere-neutral-row-element-border-color: #495865;
// Neutral row - icons colors
$vsphere-neutral-row-icon-success-color: $vsphere-color-success-300;
$vsphere-neutral-row-icon-error-color: $vsphere-color-danger-300;
$vsphere-neutral-row-icon-warning-color: #FDD006;
$vsphere-neutral-row-icon-info-color: $vsphere-neutral-row-link-color;
// TODO revise for dark theme
$vsphere-neutral-icon-color-highlight: #0079b8; // TODO to be provided by UX, spread to hover and selected states
// Neutral row - forms colors
$vsphere-neutral-row-forms-icon-color: $emphasis-color;
$vsphere-neutral-row-forms-icon-hover-color: $vsphere-color-neutral-600;
$vsphere-neutral-row-forms-border-color: $vsphere-color-neutral-50;
// Neutral row - outline colors
$vsphere-neutral-row-outline-color: #49AEDA;
$vsphere-neutral-row-outline-width: $vsphere-outline-width;
$vsphere-neutral-row-outline-style: $vsphere-outline-style;
$vsphere-neutral-row-outline-offset: $vsphere-outline-offset;
// Neutral row - emphasis colors (labels. badges, alerts)
$vsphere-neutral-row-emphasis-info: $vsphere-neutral-row-icon-info-color;
$vsphere-neutral-row-emphasis-success: $vsphere-neutral-row-icon-success-color;
$vsphere-neutral-row-emphasis-warning: $vsphere-neutral-row-icon-warning-color;
$vsphere-neutral-row-emphasis-danger: $vsphere-neutral-row-icon-error-color;
$vsphere-neutral-row-emphasis-purple: $vsphere-color-secondary-action-purple-200;
$vsphere-neutral-row-emphasis-blue: $vsphere-neutral-row-link-color;
$vsphere-neutral-row-emphasis-orange: $vsphere-neutral-row-icon-warning-color;
$vsphere-neutral-row-emphasis-light-blue: $vsphere-color-action-blue-700;
$vsphere-neutral-row-emphasis-gray: $vsphere-neutral-row-text-color;
$vsphere-neutral-row-emphasis-gray-inverted: $vsphere-neutral-row-bg-color;

// Hover row colors
$vsphere-hover-row-bg-color: $light-gray;
$vsphere-hover-row-border-bottom-color: $emphasis-color; // patch for Clarity
$vsphere-hover-row-text-color: #E9ECEF; // patch for Clarity
$vsphere-hover-row-link-color: #79C6E6; // patch for Clarity
$vsphere-hover-row-group-bg-color: $vsphere-hover-row-bg-color;
$vsphere-hover-row-group-text-color: $vsphere-hover-row-text-color;
$vsphere-hover-row-element-border-color: $emphasis-color;
// Hovered row - icons colors
$vsphere-hover-row-icon-success-color: $vsphere-color-success-200;
$vsphere-hover-row-icon-error-color: $vsphere-color-danger-200;
$vsphere-hover-row-icon-warning-color: #FDD006;
$vsphere-hover-row-icon-info-color: $vsphere-hover-row-link-color;
// Hovered row - forms colors
$vsphere-hover-row-forms-icon-color: $emphasis-color;
$vsphere-hover-row-forms-icon-hover-color: $vsphere-color-neutral-0;
$vsphere-selected-row-forms-border-color: $vsphere-color-neutral-500;
// Hovered row - outline colors
$vsphere-hover-row-outline-color: #49AEDA;
$vsphere-hover-row-outline-width: $vsphere-outline-width;
$vsphere-hover-row-outline-style: $vsphere-outline-style;
$vsphere-hover-row-outline-offset: $vsphere-outline-offset;
// Hovered row - emphasis colors (labels. badges, alerts)
$vsphere-hover-row-emphasis-info: $vsphere-hover-row-icon-info-color;
$vsphere-hover-row-emphasis-success: $vsphere-hover-row-icon-success-color;
$vsphere-hover-row-emphasis-warning: $vsphere-hover-row-icon-warning-color;
$vsphere-hover-row-emphasis-danger: $vsphere-hover-row-icon-error-color;
$vsphere-hover-row-emphasis-purple: $vsphere-color-secondary-action-purple-200;
$vsphere-hover-row-emphasis-blue: $vsphere-hover-row-link-color;
$vsphere-hover-row-emphasis-orange: $vsphere-hover-row-icon-warning-color;
$vsphere-hover-row-emphasis-light-blue: $vsphere-color-action-blue-700;
$vsphere-hover-row-emphasis-gray: $vsphere-hover-row-text-color;
$vsphere-hover-row-emphasis-gray-inverted: $vsphere-color-neutral-800;

// Selected row colors
$vsphere-selected-row-bg-color: $inverted-gray;
$vsphere-selected-row-text-color: $vsphere-color-neutral-1000;
$vsphere-selected-row-link-color: $light-blue; // not supported by Clarity, patched
$vsphere-selected-row-element-border-color: #495865;
// Selected row - icons colors
$vsphere-selected-row-icon-success-color: $vsphere-color-success-800;
$vsphere-selected-row-icon-error-color: $vsphere-color-danger-900;
$vsphere-selected-row-icon-warning-color: $vsphere-color-warning-1000;
$vsphere-selected-row-icon-info-color: $vsphere-selected-row-link-color;
// Selected row - forms colors
$vsphere-selected-row-forms-icon-color: $vsphere-color-neutral-700;
$vsphere-selected-row-forms-icon-hover-color: $vsphere-color-neutral-1000;
// Selected row - outline colors
$vsphere-selected-row-outline-color: #1487B8;
$vsphere-selected-row-outline-width: $vsphere-outline-width;
$vsphere-selected-row-outline-style: $vsphere-outline-style;
$vsphere-selected-row-outline-offset: $vsphere-outline-offset;
// Selected row - emphasis colors (labels. badges, alerts)
$vsphere-selected-row-emphasis-info: $vsphere-selected-row-icon-info-color;
$vsphere-selected-row-emphasis-success: $vsphere-selected-row-icon-success-color;
$vsphere-selected-row-emphasis-warning: $vsphere-selected-row-icon-warning-color;
$vsphere-selected-row-emphasis-danger: $vsphere-selected-row-icon-error-color;
$vsphere-selected-row-emphasis-purple: $vsphere-color-secondary-action-purple-700;
$vsphere-selected-row-emphasis-blue: $vsphere-selected-row-link-color;
$vsphere-selected-row-emphasis-orange: $vsphere-selected-row-icon-warning-color;
$vsphere-selected-row-emphasis-light-blue: $vsphere-color-action-blue-700;
$vsphere-selected-row-emphasis-gray: $vsphere-selected-row-text-color;
$vsphere-selected-row-emphasis-gray-inverted: $vsphere-selected-row-bg-color;

// Chevron icon color
$vsphere-neutral-chevron-icon-color: $vsphere-neutral-row-text-color;
$vsphere-expanded-chevron-icon-color: $vsphere-selected-row-text-color; // patch for Clarity
$vsphere-expandable-hover-chevron-icon-color: #D8E3E9; // patch for Clarity

// Grids, Tables
$vsphere-table-border-color: $vsphere-neutral-row-element-border-color;