@import "clr-ui-a11y.overrides.scss";

/*================================================================================*/
/*                         DARK THEME OVERRIDES                                   */
/*================================================================================*/

/*================================================================================*/
/* Overwrite the default variable assignments in _tables.clarity.scss here:       */
/*================================================================================*/
$clr-table-bgcolor: $vsphere-neutral-row-bg-color;
$clr-thead-bgcolor: $vsphere-neutral-row-bg-color;

/*================================================================================*/
/* Overwrite the default variable assignments in _modal.clarity.scss here:        */
/*================================================================================*/
$clr-modal-bg-color: $vsphere-neutral-row-bg-color;
$clr-modal-close-color: $clr-color-neutral-50;

/*================================================================================*/
/* Overwrite the default variable assignments in _dropdown.clarity.scss here:     */
/*================================================================================*/
$clr-dropdown-bg-color: $vsphere-global-bg-color;
$clr-dropdown-text-color: $vsphere-neutral-row-text-color;
$clr-dropdown-item-color: $vsphere-neutral-row-text-color;
$clr-dropdown-active-text-color: $vsphere-selected-row-text-color;
$clr-dropdown-bg-hover-color: $vsphere-hover-row-bg-color;

/*================================================================================*/
/* Overwrite the default variable assignments in _stackview.clarity.scss here:    */
/*================================================================================*/
$clr-stack-view-stack-block-expanded-bg-color: $vsphere-selected-row-bg-color;
$clr-stack-view-stack-block-expanded-text-color: $vsphere-selected-row-text-color;
$clr-stack-view-stack-block-label-and-content-bg-color: $vsphere-neutral-row-bg-color;
$clr-stack-view-stack-block-content-text-color: $vsphere-neutral-row-text-color;

// Default Button
$clr-btn-default-outline-color: $vsphere-color-action-blue-300;
// Link Button
$clr-btn-link-color: $vsphere-color-action-blue-300;

// Table
$clr-table-font-color: $vsphere-selected-row-text-color;;