// Clarity patches: for not supported states
.stack-view {
   .stack-block-expandable {
      &.on-focus:not(.stack-block-expanded),
      &:hover:not(.stack-block-expanded) {
         color: #{$vsphere-hover-row-text-color};
         border-bottom: 1px solid $vsphere-hover-row-border-bottom-color;
         &:last-of-type {
            border-bottom: 1px solid $vsphere-hover-row-border-bottom-color;
         }
         @include hoveredRowChildElements();
         // Includes nested stack-block
         .stack-block-content {
            color: #{$vsphere-hover-row-text-color};
            background-color: inherit;
         }
      }
      color: $vsphere-neutral-row-text-color;
      background-color: $vsphere-neutral-row-bg-color;
      @include neutralRowChildAndTopLevelElements();
   }
   //border-bottom: 1px solid $vsphere-table-border-color;

   .stack-block-expanded {
      > .stack-block-label {
         color: $vsphere-selected-row-text-color;
         @include selectedRowChildElements();
         background-color: $vsphere-selected-row-bg-color;
         .stack-block-caret {
            fill: #{$vsphere-expanded-chevron-icon-color};
         }

         // Includes nested stack-block
         .stack-block-content {
            color: $vsphere-selected-row-text-color;
            background-color: inherit;
         }
      }
   }
}

// This code is only for overriding legacy custom components in vm-ui:
// "VM -> Edit Settings".
// Should be removed when the legacy code is rewritten with
// Clarity components - VSUIP-4390.
@mixin expandedStackBlockContent () {
   .select {
      select {
         color: $vsphere-selected-row-text-color;
         border-bottom: $vsphere-table-borderwidth solid $vsphere-selected-row-text-color;
         @include placeholderFocused($vsphere-selected-row-text-color);
      }
      &::after {
         background-image: generateCaretIcon($vsphere-selected-row-text-color);
      }

      &::after:hover {
         color: $vsphere-selected-row-forms-icon-hover-color;
      }
   }
   .vui-dropdown-vxValue .input-group-btn>button.btn-vxValue>clr-icon.dropdown-icon-vxValue {
      color: $vsphere-selected-row-text-color;
   }
}

@mixin expandableNotExpandedStackBlockContent (
   $text-color: $vsphere-neutral-row-text-color,
   $hover-icon-color: $vsphere-neutral-row-forms-icon-hover-color) {
   .select {
      select {
         color: $text-color;
         border-bottom: $vsphere-table-borderwidth solid $text-color;
         @include placeholderFocused($text-color);
      }
      &::after {
         background-image: generateCaretIcon($text-color);
      }

      &::after:hover {
         color: $hover-icon-color;
      }
   }
}

.vm-stack-view {
   .select {
      &::after {
         background-image: generateCaretIcon($vsphere-neutral-row-text-color);
      }

      &::after:hover {
         color: $vsphere-neutral-row-forms-icon-hover-color;
      }

   }

   .stack-block-expandable.stack-block-expanded {
      > .stack-block-content {
         @include expandedStackBlockContent();
      }
      > .stack-block-label {
         .stack-block-content {
            @include expandedStackBlockContent();
         }
      }

   }
   .stack-block-expandable:not(.stack-block-expanded) {
      &:hover {
         > .stack-block-content {
            @include expandableNotExpandedStackBlockContent(
                  $vsphere-hover-row-text-color,
                  $vsphere-hover-row-forms-icon-hover-color);
         }
      }
      > .stack-block-content {
         @include expandableNotExpandedStackBlockContent();
      }
   }
}