$vsphere-color-hue: 198 !default;
$vsphere-color-neutral-saturation: 0% !default;


$vsphere-color-neutral-0: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 100%) !default; //  #fff - white
$vsphere-color-neutral-50: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 98%) !default; //  #fafafa - near white
$vsphere-color-neutral-100: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 95%) !default; // #f2f2f2
$vsphere-color-neutral-200: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 93%) !default; // #ededed
$vsphere-color-neutral-300: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 87%) !default; // #dedede
$vsphere-color-neutral-400: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 80%) !default; // #ccc
$vsphere-color-neutral-500: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 60%) !default; // #999999 - middle grey
$vsphere-color-neutral-600: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 45%) !default; // #8c8c8c
$vsphere-color-neutral-700: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 34%) !default; // #575757
$vsphere-color-neutral-800: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 27%) !default; // #454545
$vsphere-color-neutral-900: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 19%) !default; // #303030 - dark grey
$vsphere-color-neutral-1000: hsl($vsphere-color-hue, $vsphere-color-neutral-saturation, 0%) !default; // #000 - black

