// This variable is needed, because Clarity Dark theme
// does not use css custom properties (only the Light theme uses them)
// and also Clarity's $clr-use-custom-properties variable is not overridable.
// This forces us to maintain the same behaviour and add some selectors only
// for the case of Dark theme (which overrides $vsphere-css-custom-properties
// to false).
$vsphere-css-custom-properties: true !default;

// Common outline
$vsphere-outline-width: 2px !default;
$vsphere-outline-style: solid !default;
$vsphere-outline-offset: -2px !default;

// Common colors
$light-gray: #E8E8E8 !default;
$inverted-gray: #29414E !default;
$emphasis-color: #666666 !default;

// Common border
$vsphere-border-style: solid !default;
$vsphere-border-width: 1px !default;

// Blue
$light-blue: #79C6E6 !default;

// Disabled forms colors - Clarity 4 specific. Were added in Clarity 4,
// as replacement of setting opacity: 0.54 as in Clarity 3.
$vsphere-disabled-forms-color: #B3B3B3 !default; //original $clr-color-neutral-500
$vsphere-disabled-forms-secondary-color: #8C8C8C !default; //original $clr-color-neutral-600
$vsphere-forms-placeholder-color: #8C8C8C !default; //original $clr-color-neutral-600

// Neutral row colors
$vsphere-neutral-row-bg-color: $vsphere-color-neutral-0 !default;
$vsphere-neutral-row-text-color: #565656 !default;
$vsphere-neutral-row-link-color: #0072A3 !default;
$vsphere-neutral-row-element-border-color: #CCCCCC !default;
// Neutral row - icons colors
$vsphere-neutral-row-icon-success-color: $vsphere-color-success-700 !default;
$vsphere-neutral-row-icon-error-color: #DB2100 !default;
$vsphere-neutral-row-icon-warning-color: $vsphere-color-warning-1000 !default;
$vsphere-neutral-row-icon-info-color: $vsphere-neutral-row-link-color !default;
$vsphere-neutral-icon-color-highlight: #0079b8 !default; // TODO to be provided by UX, spread to hover and selected states
// Neutral row - forms colors
$vsphere-neutral-row-forms-icon-color: $emphasis-color !default;
$vsphere-neutral-row-forms-icon-hover-color: $vsphere-color-neutral-600 !default;
$vsphere-neutral-row-forms-border-color: $vsphere-color-neutral-700 !default;
$vsphere-neutral-row-forms-placeholder-color: $vsphere-forms-placeholder-color !default;
// Neutral row - disabled forms colors
$vsphere-neutral-row-disabled-forms-text-color: $vsphere-disabled-forms-color !default;
$vsphere-neutral-row-disabled-forms-label-color: $vsphere-disabled-forms-secondary-color !default;
$vsphere-neutral-row-disabled-forms-border-color: $vsphere-disabled-forms-color !default;
$vsphere-neutral-row-disabled-forms-subtext-color: $vsphere-disabled-forms-secondary-color !default;
// Neutral row - outline colors
$vsphere-neutral-row-outline-color: #1487B8 !default;
$vsphere-neutral-row-outline-width: $vsphere-outline-width !default;
$vsphere-neutral-row-outline-style: $vsphere-outline-style !default;
$vsphere-neutral-row-outline-offset: $vsphere-outline-offset !default;
// Neutral row - emphasis colors (labels. badges, alerts)
