// TODO remove this after get rid of legacy components
$styledInputs: 'input[type=text], input[type=password], input[type=number], input[type=email], input[type=url], input[type=tel], input[type=date], input[type=time], input[type=datetime-local]';
@mixin input (
   $text-color,
   $border-color,
   $outline-color,
   $validation-error-color) {
   #{$styledInputs},
   .clr-input-wrapper .clr-input,
   .clr-input-group {
      color: $text-color;
      @if (not $vsphere-css-custom-properties) {
         border-bottom-color: $border-color;
      }
      // TODO remove this after get rid of legacy components
      &.has-error {
         border-color: $vsphere-border-width $vsphere-border-style $validation-error-color;
      }

      @include placeholderFocused($text-color);
   }
}