@import "../icons/clr-icons.patch";
@import "../forms/clr-forms.patch";
@import "../forms/styles/clr-containers.patch";
@import "../typography/clr-typography.patch";
@import "../emphasis/clr-labels.patch";
@import "../emphasis/clr-badges.patch";
@import "../data/datagrid/clr-datagrid.patch";
@import "../button/clr-buttons.patch";
@import "../popover/dropdown/clr-dropdown.patch";
@import "../data/clr-table.patch";

@mixin nestableContainerElements(
   $parentRowState: $vsphere-row-neutral,
   $nest-level: $vsphere-max-nest-level) {
   @include nestableDatagrid($parentRowState, $nest-level);
}

$ancestor-state-colors: (
   "text": ($vsphere-row-neutral: $vsphere-neutral-row-text-color,
      $vsphere-row-hover: $vsphere-hover-row-text-color,
      $vsphere-row-selected: $vsphere-selected-row-text-color),
   "bg": ($vsphere-row-neutral: $vsphere-neutral-row-bg-color,
      $vsphere-row-hover: $vsphere-hover-row-bg-color,
      $vsphere-row-selected: $vsphere-selected-row-bg-color),
   "link": ($vsphere-row-neutral: $vsphere-neutral-row-link-color,
      $vsphere-row-hover: $vsphere-hover-row-link-color,
      $vsphere-row-selected: $vsphere-selected-row-link-color),
   "border": ($vsphere-row-neutral: $vsphere-neutral-row-element-border-color,
      $vsphere-row-hover: $vsphere-hover-row-element-border-color,
      $vsphere-row-selected: $vsphere-selected-row-element-border-color));

@function colorForState($property, $ancestorRowState) {
   $property-color-for-ancestor-state: map-get(map-get($ancestor-state-colors, $property), $ancestorRowState);
   @return $property-color-for-ancestor-state;
}

@mixin neutralRowChildAndTopLevelElements() {
   .btn-link, a, a:link {
      color: $vsphere-neutral-row-link-color;
   }

   @include vsphereNeutralRowFormFields();
   @include vsphereNeutralRowFormContainers();
   @include vsphereNeutralRowClrIcon();
   @include vsphereNeutralRowTypography();
   @include vsphereNeutralRowLabel();
   @include vsphereNeutralRowBadge();
   @include vsphereNeutralRowButton();
   @include vsphereNeutralRowDropdown();
   @include vsphereNeutralRowTable();
}

@mixin hoveredRowChildElements() {
   @include hoveredRowOutlines();

   .btn-link, a {
      color: $vsphere-hover-row-link-color;
   }

   @include vsphereHoveredRowFormFields();
   @include vsphereHoveredRowFormContainers();
   //@include vsphereHoveredRowClrIcon();
   @include vsphereHoveredRowTypography();
   @include vsphereHoveredRowLabel();
   //@include vsphereHoveredRowBadge();
   @include vsphereHoveredRowButton();
}

@mixin selectedRowChildElements() {
   .signpost-content-body {
      .btn-link, a {
         color: $vsphere-neutral-row-link-color;
      }
      @include neutralRowOutlines();
      @include vsphereNeutralRowClrIcon();
      @include vsphereNeutralRowTypography();
   }
   .btn-link, a {
      color: $vsphere-selected-row-link-color;
   }
   @include selectedRowOutlines();
   @include vsphereSelectedRowFormFields();
   @include vsphereSelectedRowFormContainers();
   @include vsphereSelectedRowClrIcon();
   @include vsphereSelectedRowTypography();
   @include vsphereSelectedRowLabel();
   @include vsphereSelectedRowBadge();
   @include vsphereSelectedRowButton();
   @include vsphereSelectedRowDropdown();
   @include vsphereSelectedRowTable();
}

@mixin focusGlobalHandler() {
   *:focus {
      outline: none;
   }

   body:not(.user-tabbing) {
      *:focus {
         outline: none;
      }
   }

   .user-tabbing {
      @include focusedElementOutline();
      .main-nav HEADER {
         *:not(input) {
            &:focus {
               outline: $vsphere-selected-row-outline-width $vsphere-selected-row-outline-style $vsphere-selected-row-outline-color;
               outline-offset: $vsphere-selected-row-outline-offset;
            }
         }
      }
   }
}

@mixin neutralRowOutlines() {
   @include focusedInnerElementOutline();
}

@mixin hoveredRowOutlines() {
   @include focusedInnerElementOutline(
         $vsphere-hover-row-outline-width,
         $vsphere-hover-row-outline-style,
         $vsphere-hover-row-outline-color,
         $vsphere-hover-row-outline-offset);
}

@mixin selectedRowOutlines {
   @include focusedInnerElementOutline(
         $vsphere-selected-row-outline-width,
         $vsphere-selected-row-outline-style,
         $vsphere-selected-row-outline-color,
         $vsphere-selected-row-outline-offset);
}

@mixin focusedInnerElementOutline(
   $outline-width: $vsphere-neutral-row-outline-width,
   $outline-style: $vsphere-neutral-row-outline-style,
   $outline-color: $vsphere-neutral-row-outline-color,
   $outline-offset: $vsphere-neutral-row-outline-offset) {
   body.user-tabbing & {
      @include focusedElementOutline($outline-width,
            $outline-style,
            $outline-color,
            $outline-offset);
   }
}

@mixin focusedElementOutline($outline-width: $vsphere-neutral-row-outline-width,
   $outline-style: $vsphere-neutral-row-outline-style,
   $outline-color: $vsphere-neutral-row-outline-color,
   $outline-offset: $vsphere-neutral-row-outline-offset) {
   &:focus,
   a:focus,
   button:focus,
   [role="button"]:focus,
   .dropdown-icon:focus,
   .menuItem-container:focus,
   [role="tab"]:focus {
      outline: $outline-width $outline-style $outline-color;
      outline-offset: $outline-offset;
   }

   button:not(.nav-link):focus,
   .clr-tree-node-content-container:focus,
   .label.clickable:focus {
      outline: $outline-width $outline-style $outline-color;
      outline-offset: 1px;
   }

  .cdk-tree-node:focus {
    outline-style: none;
    .vsc-tree-node-text {
      box-shadow: inset 0 0 0 2px $outline-color;
    }
  }

   button.nav-link.active:focus {
      outline-offset: -3px;
   }

   // Checkboxes and toggles
   .clr-checkbox-wrapper,
   .clr-toggle-wrapper {
      input[type='checkbox']:focus + label::before,
      input[type='checkbox']:focus:checked + label::before {
         outline: $outline-width $outline-style $outline-color;
         outline-offset: 1px;
      }
   }

   // This is needed only for the VUI cases - should be deleted
   // after get rid of VUI.
   input[type='checkbox']:focus,
   input[type='checkbox']:focus:checked {
      outline: $outline-width $outline-style $outline-color;
      outline-offset: 1px;
   }

   // Radio
   .clr-radio-wrapper,
   .radio,
   .radio-inline {
      input[type='radio']:focus + label::before,
      input[type='radio']:focus:checked + label::before {
         outline: $outline-width $outline-style $outline-color;
         //box-shadow: inset 0 0 0 0.3rem $outline-color;
         outline-offset: 1px;
      }
   }

   // Input
   .clr-input-wrapper {
      input:focus {
         outline: $outline-width $outline-style $outline-color;
         outline-offset: 2px;
      }
   }

   // Textarea
   .clr-textarea-wrapper {
      .clr-textarea:focus {
         outline: $outline-width $outline-style $outline-color;
         box-shadow: inset 0 0 0;
         outline-offset: 1px;
      }
   }

   // Select
   .clr-select-wrapper {
      select:focus {
         outline: $outline-width $outline-style $outline-color;
         outline-offset: 1px;
      }
   }
}

@include neutralRowChildAndTopLevelElements();
@include nestableContainerElements();
@include focusGlobalHandler();
