@mixin select (
   $text-color,
   $border-color,
   $icon-color,
   $icon-hover-color,
   $validation-error-color
) {
   .clr-select-wrapper,
   .clr-multiselect-wrapper,
   .form-block .select {
      select {
         color: $text-color;
         @if (not $vsphere-css-custom-properties) {
            border-bottom-color: $border-color;
         }
         @include placeholderFocused($text-color);
      }

      &::after {
         background-image: generateCaretIcon($icon-color);
      }

      &:hover::after {
         color: $icon-hover-color;
      }
   }
   .clr-error select:not([multiple]) {
      border-bottom-color: $validation-error-color;
   }
}