// Clarity patches: for not supported states
.clr-vertical-nav {
   .nav-link   {
      border-bottom: $vsphere-table-borderwidth solid transparent;
   }

   .nav-link:hover:not(.active) {
      background-color: $vsphere-hover-row-bg-color;
      color: $vsphere-hover-row-text-color;
      border-bottom: $vsphere-table-borderwidth solid $vsphere-hover-row-border-bottom-color;
      @include hoveredRowChildElements();
   }

   .nav-link.active {
      background-color: $vsphere-selected-row-bg-color;
      color: $vsphere-selected-row-text-color;
      border-bottom: $vsphere-table-borderwidth solid transparent;
      @include selectedRowChildElements();
   }

   .nav-group-content {
      .nav-group-trigger {
         border-bottom: $vsphere-table-borderwidth solid transparent;
         &:hover {
            color: $vsphere-hover-row-group-text-color;
            background-color: $vsphere-hover-row-group-bg-color;
            border-bottom: $vsphere-table-borderwidth solid $vsphere-hover-row-border-bottom-color;
            @include hoveredRowChildElements();
         }
      }
   }
}