.clr-tree-node {
   .clr-treenode-link {
      &:hover,
      &:focus {
         color: $vsphere-hover-row-text-color;
         border-bottom: 1px solid $vsphere-hover-row-border-bottom-color;
         @include vsphereHoveredRowClrIcon();
      }

      &.active {
         background: $clr-tree-link-selection-color;
         color: $vsphere-selected-row-text-color;
         border-bottom: 1px solid transparent;
         @include vsphereSelectedRowClrIcon();
      }

      @include vsphereNeutralRowClrIcon();
      border-bottom: 1px solid transparent;
   }
}