@mixin vsphereNeutralRowFormContainers () {
   @include formContainers();
}

@mixin vsphereHoveredRowFormContainers () {
   @include formContainers(
         $vsphere-hover-row-text-color,
         $vsphere-hover-row-icon-error-color);
}

@mixin vsphereSelectedRowFormContainers () {
   @include formContainers(
         $vsphere-selected-row-text-color,
         $vsphere-selected-row-icon-error-color);
}

@mixin formContainers (
   $subtext-color: $vsphere-neutral-row-text-color,
   $subtext-error-color: $vsphere-neutral-row-icon-error-color,
   $disabled-forms-text-color: $vsphere-neutral-row-disabled-forms-text-color,
   $disabled-forms-label-color: $vsphere-neutral-row-disabled-forms-label-color,
   $disabled-forms-border-color: $vsphere-neutral-row-disabled-forms-border-color,
   $disabled-forms-subtext-color: $vsphere-neutral-row-disabled-forms-subtext-color) {
   .clr-subtext {
      color: $subtext-color;
   }
   .clr-error {
      .clr-subtext {
         color: $subtext-error-color;
      }
   }

   .clr-form-control-disabled {
      color: $disabled-forms-text-color;

      label,
      .clr-control-label {
         color: $disabled-forms-label-color;
      }

      .clr-input,
      .clr-textarea,
      .clr-select {
         color: $disabled-forms-text-color;
         border-bottom-color: $disabled-forms-border-color;
      }

      input[type='range']::-webkit-slider-thumb {
         background-color: $disabled-forms-border-color;
      }

      .clr-subtext {
         color: $disabled-forms-subtext-color;
      }
   }
}
