@mixin vsphereNeutralRowClrIcon() {
   @include vsphereClrIcon();
}

@mixin vsphereHoveredRowClrIcon() {
   @include vsphereClrIcon(
      $vsphere-hover-row-text-color,
      $vsphere-hover-row-icon-success-color,
      $vsphere-hover-row-icon-error-color,
      $vsphere-hover-row-icon-warning-color,
      $vsphere-hover-row-link-color,
      $vsphere-hover-row-text-color
   );
}

@mixin vsphereSelectedRowClrIcon() {
   @include vsphereClrIcon(
      $vsphere-selected-row-text-color,
      $vsphere-selected-row-icon-success-color,
      $vsphere-selected-row-icon-error-color,
      $vsphere-selected-row-icon-warning-color,
      $vsphere-selected-row-link-color,
      $vsphere-selected-row-text-color
   );
}

@mixin setIconColor($clr-icon-color) {
   fill: $clr-icon-color;
   .transparent-fill-stroke {
      stroke: $clr-icon-color;
   }
}

@mixin vsphereClrIcon(
   $icon-color: $vsphere-neutral-row-text-color,
   $icon-color-success: $vsphere-neutral-row-icon-success-color,
   $icon-color-error: $vsphere-neutral-row-icon-error-color,
   $icon-color-warning: $vsphere-neutral-row-icon-warning-color,
   $icon-color-info: $vsphere-neutral-row-icon-info-color,
   $icon-color-inverse: $vsphere-neutral-row-text-color,
   $icon-color-highlight: $vsphere-neutral-icon-color-highlight,
   $icon-color-danger: $vsphere-neutral-row-icon-error-color
) {
   clr-icon {
      & {
         fill: $icon-color;
      }
      &.is-green,
      &.is-success,
      &.clr-accordion-complete-icon {
         @include setIconColor($icon-color-success);
      }
      &.is-red,
      &.is-danger,
      &.is-error,
      &.clr-accordion-error-icon,
      &.clr-validate-icon {
         @include setIconColor($icon-color-error);
      }
      &.is-warning {
         @include setIconColor($icon-color-warning);
      }
      &.is-blue,
      &.is-info,
      &[shape='info'] {
         // ugly patch: class .is-info is missing in Clarity
         @include setIconColor($icon-color-info);
      }
      &.is-white,
      &.is-inverse {
         @include setIconColor($icon-color-inverse);
      }
      &.is-highlight {
         @include setIconColor($icon-color-highlight);
         .clr-i-badge {
            @include setIconColor($icon-color-danger);
         }
         &.has-badge--success {
            .clr-i-badge {
               @include setIconColor($icon-color-success);
            }
         }
         &.has-badge--error {
            .clr-i-badge {
               @include setIconColor($icon-color-error);
            }
         }
         &.has-badge--info {
            .clr-i-badge {
               @include setIconColor($icon-color-info);
            }
         }
         &.has-alert {
            .clr-i-alert {
               @include setIconColor($icon-color-warning);
            }
         }
      }
   }
   // Patch for legacy components, to be removed - VSUIP-4390
   .error-child-component span::after {
      content: '';
      position: absolute;
      height: 1rem;
      width: 1rem;
      background-image: generateErrorIcon($icon-color-error);
      background-size: contain;
      vertical-align: middle;
      margin-left: 18px;
   }
   // Patch for legacy components, to be removed - VSUIP-4390
   .vsphere-icon-action-remove {
      background: none;
      background-image: generateRemoveIcon($icon-color-inverse);
   }
}
