@import "./styles/clr-checkbox.patch";
@import "./styles/clr-toggle.patch";
@import "./styles/clr-radio.patch";
@import "./styles/clr-select.patch";
@import "./styles/clr-input.patch";
@import "./styles/clr-textarea.patch";
@import "./styles/clr-datepicker.patch";

@mixin placeholderFocused ($forms-text-color) {
   &:focus:not([readonly]) {
      &::placeholder {
         color: $forms-text-color;
      }
   }
}

@mixin vsphereNeutralRowFormFields () {
   @include form-fields-appearance();
}

@mixin vsphereHoveredRowFormFields () {
   @include form-fields-appearance(
         $vsphere-hover-row-text-color,
         $vsphere-hover-row-bg-color,
         $vsphere-hover-row-forms-border-color,
         $vsphere-hover-row-forms-icon-color,
         $vsphere-hover-row-forms-icon-hover-color,
         $vsphere-hover-row-icon-error-color,
         $vsphere-hover-row-outline-color);
}

@mixin vsphereSelectedRowFormFields () {
   @include form-fields-appearance(
         $vsphere-selected-row-text-color,
         $vsphere-selected-row-bg-color,
         $vsphere-selected-row-forms-border-color,
         $vsphere-selected-row-forms-icon-color,
         $vsphere-selected-row-forms-icon-hover-color,
         $vsphere-selected-row-icon-error-color,
         $vsphere-selected-row-outline-color);
}

@mixin form-fields-appearance(
   $forms-text-color: $vsphere-neutral-row-text-color,
   $forms-bg-color: $vsphere-neutral-row-bg-color,
   $forms-border-color: $vsphere-color-neutral-700,
   $forms-icon-color: $vsphere-neutral-row-forms-icon-color,
   $forms-icon-hover-color: $vsphere-neutral-row-forms-icon-hover-color,
   $forms-validation-error-color: $vsphere-neutral-row-icon-error-color,
   $outline-color: $vsphere-neutral-row-outline-color) {
   label {
      color: $forms-text-color;
   }
   @include overrideBrowserAutoFill($forms-text-color, $forms-bg-color);
   @include checkbox($forms-text-color, $outline-color);
   @include toggle($forms-text-color);
   @include radio($forms-text-color, $outline-color);
   @include input(
         $forms-text-color,
         $forms-border-color,
         $outline-color,
         $forms-validation-error-color);
   @include textarea(
         $forms-text-color,
         $forms-bg-color,
         $vsphere-border-width,
         $forms-border-color,
         $forms-validation-error-color);
   @include select(
         $forms-text-color,
         $forms-border-color,
         $forms-icon-color,
         $forms-icon-hover-color,
         $forms-validation-error-color,
   );
   @include datepicker($outline-color);
}

@mixin overrideBrowserAutoFill(
   $forms-text-color: $vsphere-neutral-row-text-color,
   $forms-bg-color: $vsphere-neutral-row-bg-color) {
   // Change autocomplete styles in WebKit
   input:-webkit-autofill,
   input:-webkit-autofill:hover,
   input:-webkit-autofill:focus,
   textarea:-webkit-autofill,
   textarea:-webkit-autofill:hover,
   textarea:-webkit-autofill:focus,
   select:-webkit-autofill,
   select:-webkit-autofill:hover,
   select:-webkit-autofill:focus {
      -webkit-text-fill-color: $forms-text-color;
      -webkit-box-shadow: 0 0 0px 1000px $forms-bg-color inset;
      transition: background-color 5000s ease-in-out 0s;
   }

   // Disable Firefox filtering
   input,
   select,
   textarea {
      filter: none;
   }
}