// Importing common custom properties for dark and light themes
@import "clr-ui-a11y.custom.properties";

// Dark theme specific custom properties
:root {
   --clr-table-bgcolor: #{$clr-table-bgcolor};
   --clr-thead-bgcolor: #{$clr-thead-bgcolor};
   --clr-modal-bg-color: #{$clr-modal-bg-color};
   --clr-modal-close-color: #{$clr-modal-close-color};
   --clr-dropdown-bg-color: #{$clr-dropdown-bg-color};
   --clr-dropdown-text-color: #{$clr-dropdown-text-color};
   --clr-dropdown-item-color: #{$clr-dropdown-item-color};
   --clr-dropdown-active-text-color: #{$clr-dropdown-active-text-color};
   --clr-btn-default-outline-color: #{$clr-btn-default-outline-color};
   --clr-btn-link-color: #{$clr-btn-link-color};
}