.dropdown,
.tabs-overflow {
  .dropdown-menu {
    &:not(.btn-link) {
      input::placeholder {
         color: $vsphere-neutral-row-text-color;
      }
    }

    .btn,
    .dropdown-item {
      &:hover {
        .nav-link {
          background-color: $vsphere-hover-row-bg-color;
          color: $vsphere-hover-row-text-color;
          border-bottom: 1px solid $vsphere-hover-row-border-bottom-color;
          .btn-link, a {
            color: $vsphere-hover-row-link-color;
          }
        }
        border-bottom: 1px solid $vsphere-hover-row-border-bottom-color;
        //@include hoveredRowChildElements();
      }
      border-bottom: 1px solid transparent;

      &.active {
        background-color: $vsphere-selected-row-bg-color;
        color: $vsphere-selected-row-text-color;
        border-bottom: 1px solid $vsphere-selected-row-bg-color;
        .btn-link, a {
          color: $vsphere-selected-row-link-color;
        }
        border-bottom: 1px solid $vsphere-selected-row-bg-color;
        //@include selectedRowChildElements();
      }
    }
  }
}

@mixin vsphereNeutralRowDropdown() {
   @include vsphereDropdown();
}

@mixin vsphereHoverRowDropdown() {
   @include vsphereDropdown(
         $vsphere-hover-row-text-color,
         $vsphere-hover-row-border-bottom-color,
         $vsphere-table-borderwidth,
         $vsphere-hover-row-forms-icon-color,
         $vsphere-hover-row-forms-icon-hover-color
   );
}

@mixin vsphereSelectedRowDropdown() {
   @include vsphereDropdown(
         $vsphere-selected-row-text-color,
         $vsphere-selected-row-bg-color,
         $vsphere-table-borderwidth,
         $vsphere-selected-row-forms-icon-color,
         $vsphere-selected-row-forms-icon-hover-color
   );
}

@mixin vsphereDropdown(
   $text-color: $vsphere-neutral-row-text-color,
   $border-color: $vsphere-neutral-row-forms-border-color,
   $border-bottom-width: $vsphere-table-borderwidth,
   $icon-color: $vsphere-neutral-row-forms-icon-color,
   $icon-hover-color: $vsphere-neutral-row-forms-icon-hover-color) {
   .dropdown-toggle {
      &:not(.btn-link):not(.btn-primary) {
         color: $text-color;
         @include placeholderFocused($text-color);

         input {
            border-bottom: $border-bottom-width solid $border-color;
         }

         input::placeholder {
            color: $text-color;
         }
      }
   }
   .dropdown {
      .dropdown-toggle {
         &::after {
            background-image: generateCaretIcon($icon-color);
         }

         &:hover::after {
            color: $icon-hover-color;
         }
      }
   }
}