.clr-accordion-header {
   color: $vsphere-neutral-row-text-color;
   background: $vsphere-neutral-row-bg-color;
   border-bottom: $vsphere-table-borderwidth solid $vsphere-neutral-row-bg-color;
   .clr-accordion-header-button {
      color: $vsphere-neutral-row-text-color;
   }

   &:hover {
      background: $vsphere-hover-row-bg-color;
      color: $vsphere-hover-row-text-color;
      border-bottom: $vsphere-table-borderwidth solid $vsphere-hover-row-border-bottom-color;
      .clr-accordion-header-button {
         color: $vsphere-hover-row-text-color;
         @include hoveredRowChildElements();
      }
      @include hoveredRowChildElements();
   }
}

// panel expanded
.clr-accordion-panel-open {
   .clr-accordion-header {
      color: $vsphere-selected-row-text-color;
      background: $vsphere-selected-row-bg-color;
      border-bottom: $vsphere-table-borderwidth solid $vsphere-selected-row-bg-color;
      .clr-accordion-header-button {
         color: $vsphere-selected-row-text-color;
         @include selectedRowChildElements();
      }
      @include selectedRowChildElements();
   }
}